import Vue from 'vue'
import Router from 'vue-router'

const routerData = [
  { path: '/', redirect: '/home' },
  // 导航页
  { path: '/$guide$', component: () => import('../page/guide') },
  // 首页
  {
    path: '/home',
    component: () => import('../page/home'),
    meta: { title: '明星好物集赞送' }
  },
  // 详情页
  {
    path: '/detail',
    component: () => import('../page/detail'),
    meta: { title: '明星好物集赞送' }
  },
  // 分享页
  {
    path: '/share',
    component: () => import('../page/share'),
    meta: { title: '明星好物集赞送' }
  },
  // 往期精彩活动页
  {
    path: '/history',
    component: () => import('../page/history'),
    meta: { title: '往期活动精彩' }
  },
  // 我参与的活动
  {
    path: '/mine',
    component: () => import('../page/mine'),
    meta: { title: '我的活动' }
  },
  // 活动规则
  {
    path: '/rule',
    component: () => import('../page/rule'),
    meta: { title: '活动规则' }
  }
]

Vue.use(Router)
export default new Router({
  base: '/c2c/star/',
  mode: 'history',
  routes: routerData
})



