<!--倒计时模块-->

<template>
  <div class="jd-timer">
    <div class="jd-timer-item">
      {{(rest.day || 0) * 24 + rest.hour}}
    </div>
    <span>:</span>
    <div class="jd-timer-item">
      {{rest.minute}}
    </div>
    <span>:</span>
    <div class="jd-timer-item">
      {{rest.second}}
    </div>
  </div>
</template>

<script>
import { timeTransform } from '../../../assets/js/util'

export default {
  name: 'jd-timer',
  data () {
    return {
      clock: null,
      rest: {day: 0, hour: 0, minute: 0, second: 0}
    }
  },
  props: {
    data: {
      type: Number,
      default: 0
    }
  },
  created () {
    this.countTime()
    this.clock = setInterval(() => { this.countTime() }, 1000)
  },
  methods: {
    countTime () {
      let restTime = this.data - (new Date()).getTime()
      if (restTime < 1000) {
        this.$emit('endBack')
      }
      this.rest = timeTransform(this.data - (new Date()).getTime())
    }
  },
  beforeDestroy () {
    clearInterval(this.clock)
  }
}
</script>

<style type="text/scss" rel="stylesheet/scss" lang="scss">
.jd-timer {
  display: inline-block;
  vertical-align: middle;
  font-family: 'jdzh-r';
  & > span {
    display: inline-block;
    vertical-align: middle;
    padding: 0;
  }
  &-item {
    display: inline-block;
    background: #333;
    width: 28px;
    line-height: normal;
    overflow: hidden;
    border-radius: 5px;
    font-size: 20px;
    color: #fff;
    padding: 6px 0;
    text-align: center;
    vertical-align: middle;
  }
}
/*安卓下一些特殊处理*/
.underAndroid {
  .jd-timer {
    & > span {
      padding-top: 4px;
    }
  }
}
</style>



