/**
 * 分享页
 * created by qiuwei
 */
import {getUrlParam, getDeviceType, toggleLoading, toast, httpJsonp, host} from '../../assets/js/util'

// 信息查询接口   cf地址：https://cf.jd.com/pages/viewpage.action?pageId=132106459#id-明星闲置接口文档-七、活动助力页接口
const detailUrl = `${host.promotion}/collect_praise/support_detail`
// 用户点赞   cf地址：https://cf.jd.com/pages/viewpage.action?pageId=133239997#id-参与&点赞接口文档-二、点赞
const zanUrl = `${host.promotion}/collect_praise/support/click`

const state = {
  detailInfo: {} // 页面信息
}

const getters = {
  // 处理后的详情数据
  data: state => {
    return state.detailInfo || {}
  },
  // 集赞信息
  vo: state => {
    return state.detailInfo.collectPraisePartakeInfoVO || {}
  }
}

const mutations = {
  setDetailInfo (state, payload) { state.detailInfo = payload }
}

const actions = {
  /**
   * 清空页面信息
   */
  async clearDetail ({commit}) {
    commit('setDetailInfo', {})
  },
  /**
   * 页面信息查询
   */
  async getDetail ({commit}, payload) {
    let {wxInfo, notClear} = payload
    if (!notClear) {
      commit('setDetailInfo', {})
    }
    try {
      let params = {
        activityInfoId: getUrlParam('activityInfoId'),
        joinPin: encodeURIComponent(decodeURIComponent(getUrlParam('joinPin')).replace(/ /, '+'))
      }
      if (wxInfo && wxInfo.unionid) {
        // 如果当前用户有微信pin 则带入微信pin查询
        params.weixinId = wxInfo.unionid
      }
      let res = await httpJsonp(detailUrl, params)
      // 倒计时
      if (res.serverTime) {
        let now = (new Date()).getTime()
        let deviation = now - res.serverTime
        res.result.startTime += deviation
        res.result.endTime += deviation
      }
      // 合并主图和详情图
      res.result.detailImgs = (res.result.icons || []).concat(res.result.detailImgs || [])
      commit('setDetailInfo', res.result || {})
    } catch (err) {}
  },
  /**
   * 用户点赞
   */
  async zanAct ({state, getters}, payload = {}) {
    let activityInfoId = state.detailInfo.periodCode // 活动编号
    let starPin = state.detailInfo.starPin // 明星拼音名
    let goodsId = state.detailInfo.goodsId // 商品ID
    let joinPin = encodeURIComponent(decodeURIComponent(getUrlParam('joinPin')).replace(/ /, '+'))
    let ua = getDeviceType()
    let params = {
      activityAppId: 1008,
      type: 1,
      activityInfoId: activityInfoId,
      starPin: starPin,
      goodsId: goodsId,
      joinPin: encodeURIComponent(joinPin),
      virtualOrderId: encodeURIComponent(getters.vo.virtualOrderId),
      ua: ua || 'h5'
    }
    if (ua === 'wx') {
      Object.assign(params, {
        weChatOpenId: payload.openid,
        weChatUnionId: payload.unionid,
        nickName: payload.nickname,
        headImg: payload.headimgurl,
        pin: payload.unionid
      })
    }
    toggleLoading(true)
    let res = false
    try {
      await httpJsonp(zanUrl, params)
      res = true
    } catch (err) {
      toast(err.message || '操作失败')
    }
    toggleLoading(false)
    return res
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}



