<!--进度条模块-->

<template>
  <div class="jd-bar" :class="{'jd-bar--100': per === 100}">
    <div class="jd-bar-cover" :style="{width: per + '%'}">
      {{leftText || ''}}
    </div>
    <div class="jd-bar-rest" :style="{width: 100 - per + '%'}">{{rightText || ''}}</div>
  </div>
</template>

<script>
export default {
  name: 'jd-bar',
  data () {
    return {
      per: 20
    }
  },
  props: {
    percent: {
      type: Number,
      default: 20
    },
    leftText: String,
    rightText: String
  },
  watch: {
    percent (val) {
      setTimeout(() => { this.per = val }, 0)
    }
  },
  mounted () {
    setTimeout(() => { this.per = this.percent }, 0)
  }
}
</script>

<style type="text/scss" rel="stylesheet/scss" lang="scss">
.jd-bar {
  background: #fff;
  height: 100%;
  border-radius: 100px;
  font-size: 26px;
  color: #F63459;
  font-family: 'jdzh-r';
  transform: translateZ(0);
  &-cover {
    position: relative;
    display: flex;
    height: 100%;
    background: linear-gradient(-180deg, #FFEBC9 6%, #FFBD84 100%);
    border-radius: 100px 0 0 100px;
    float: left;
    justify-content: center;
    align-items: center;
    transition: all ease 1s;
    width: 20%;
    padding-top: 1px;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      right: -1px;
      width: 15px;
      height: 29px;
      background: url("../../../assets/img/barArrow.svg") no-repeat center;
      background-size: cover;
    }
  }
  &-rest {
    display: flex;
    height: 100%;
    float: right;
    border-radius: 0 100px 100px 0;
    justify-content: center;
    align-items: center;
    padding-top: 1px;
    transition: all ease 1s;
  }
  &.jd-bar--100 {
    .jd-bar-cover {
      border-radius: 100px;
      &:after {
        display: none;
      }
    }
    .jd-bar-rest {
      font-size: 0;
    }
  }
}
/*安卓下一些特殊处理*/
.underAndroid {
  .jd-bar {
    transform: none;
    .jd-bar-cover, .jd-bar-rest {
      padding-top: 5px !important;
    }
  }
}
</style>



