/**
 * 设置APP title
 * @param title
 */
export function setTitle (title) {
  if (title) {
    const para = { method: 'h5TitleEdit', params: { key1: title } }
    messageHandlers(para)
  }

  function messageHandlers (params) {
    const info = {
      version: '1.0.0',
      ua: navigator.userAgent,
      devicesType: getDevicesType()
    }
    if (info.devicesType === 1) {
      params.params = JSON.stringify(params.params)
      window.android.ppAppMessageHandler(encodeURIComponent(JSON.stringify(params)))
    } else if (info.devicesType === 2) {
      window.webkit.messageHandlers.ppAppMessageHandler.postMessage(params)
    }
    // 前进或者后退时候也会重置
    document.body.addEventListener('onpageshow', () => {
      if (info.devicesType === 1) {
        params.params = JSON.stringify(params.params)
        window.android.ppAppMessageHandler(encodeURIComponent(JSON.stringify(params)))
      } else if (info.devicesType === 2) {
        window.webkit.messageHandlers.ppAppMessageHandler.postMessage(params)
      }
    })
  }

  function getDevicesType () {
    let devicesType = 0
    const ua = navigator.userAgent
    const isAndroid = ua.indexOf('Android') > -1 || ua.indexOf('Linux') > -1 // android终端或者uc浏览器
    const isiOS = !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/) // ios终端
    if (isAndroid) {
      devicesType = 1
    } else if (isiOS) {
      devicesType = 2
    }
    return devicesType
  }
}

/**
 * 禁止页面滚动
 * @param forbid 是否禁止
 */
const forbidTouchmove = function (e) { e.preventDefault() }
export function forbidScroll (forbid = false) {
  if (forbid) {
    document.body.addEventListener('touchmove', forbidTouchmove, { passive: false })
    document.body.style.overflow = 'hidden'
  } else {
    document.body.removeEventListener('touchmove', forbidTouchmove, { passive: false })
    document.body.style.overflow = 'auto'
  }
}

/**
 * 加载中
 * @param show 是否显示
 */
export function toggleLoading (show) {
  let els = document.querySelectorAll('.myLoading')
  for (let i = 0; i < els.length; i++) {
    els[i].parentNode.removeChild(els[i])
  }
  if (!show) {
    return
  }
  let loadEl = document.createElement('div')
  loadEl.className = 'myLoading'
  loadEl.innerHTML = `
    <div style="background: url(data:image/gif;base64,) no-repeat;background-size: cover;"></div>
  `
  document.body.appendChild(loadEl)
}

/**
 * toast提示
 * @param text
 */
export function toast (text = '提示', time = 2000) {
  let els = document.querySelectorAll('.myToast')
  for (let i = 0; i < els.length; i++) {
    els[i].parentNode.removeChild(els[i])
  }
  let el = document.createElement('div')
  el.className = 'myToast'
  el.innerHTML = text
  document.body.appendChild(el)
  setTimeout(() => {
    el.style.transform = 'translate(-50%, 0)'
    el.style.opacity = 1
  }, 100)
  setTimeout(() => {
    el.style.transform = 'translate(-50%, 70%)'
    el.style.opacity = '0'
    setTimeout(() => { el.parentNode && el.parentNode.removeChild(el) }, 300)
  }, time)
}

/**
 * 埋点 文档地址：https://cf.jd.com/pages/viewpage.action?pageId=79700612
 * @param type {pv: 上报pvuv click：上报点击}
 * @param param
 * pv -> {pageName: 页面名称}
 * click -> {eventId: 事件ID, event_param: click的参数}
 */
export function logSend (param = {}, type = 'pv') {
  let setIt = () => {
    try {
      let MPing = window.MPing
      if (type === 'pv') {
        let pv = param.pageId ? new MPing.inputs.PV(param.pageId) : new MPing.inputs.PV()
        delete param.pageId
        Object.assign(pv, param)
        ;(new MPing()).send(pv)
      } else {
        let click = param.eventId ? new MPing.inputs.Click(param.eventId) : new MPing.inputs.Click()
        delete param.eventId
        Object.assign(click, param)
        click.updateEventSeries()
        ;(new MPing()).send(click)
      }
    } catch (err) {}
  }
  if (!document.querySelectorAll('script[src="//wl.jd.com/unify.min.js"]').length) {
    window.jap = {
      siteId: 'JA2018_4111189', // 站点编号，必传字段
      autoLogPv: false, // 是否自动上报pv
      anchorpvflag: false // 单页自动上报
    }
    let scri = document.createElement('script')
    scri.setAttribute('src', '//wl.jd.com/unify.min.js')
    scri.onload = () => { setIt() }
    document.head.appendChild(scri)
  } else {
    setIt()
  }
}

/**
 * get请求
 * @param url 请求路径
 * @param params 入参
 * @returns {Promise<any>} 结果promise
 */
export function httpGet (url, params = {}) {
  return new Promise((resolve, reject) => {
    axios.get(url, {params: params})
      .then(res => res.data && (['0', '200'].indexOf(String(res.data.code)) !== -1) ? resolve(res.data) : reject(res.data))
      .catch(err => reject(err))
  })
}

/**
 * post请求
 * @param url 请求路径
 * @param params 入参
 * @param isJson 是否以json的形式传递数据
 * @returns {Promise<any>} 结果promise
 */
export function httpPost (url, params = {}, isJson = false) {
  if (!isJson) {
    // axios默认以json格式(Content-Type: application/json)传递数据
    // 如果需要以Formdata(Content-Type: application/x-www-form-urlencoded)的形式传递需要做以下处理
    params = qs.stringify(params)
  }
  return new Promise((resolve, reject) => {
    axios.post(url, params)
      .then(res => res.data && (['0', '200'].indexOf(String(res.data.code)) !== -1) ? resolve(res.data) : reject(res.data))
      .catch(err => reject(err))
  })
}

/**
 * 跨域时携带cookie的get请求
 * @param url 请求路径
 * @param params 入参
 * @returns {Promise<any>} 结果promise
 */
export function httpGetWithCookie (url, params = {}) {
  return new Promise((resolve, reject) => {
    axios.create({})({
      url: url,
      method: 'get',
      params: params,
      withCredentials: true
    })
      .then(res => res.data && (['0', '200'].indexOf(String(res.data.code)) !== -1) ? resolve(res.data) : reject(res.data))
      .catch(err => reject(err))
  })
}

/**
 * 跨域时携带cookie的post请求
 * @param url 请求路径
 * @param params 入参
 * @param isJson 是否以json的形式传递数据
 * @returns {Promise<any>} 结果promise
 */
export function httpPostWithCookie (url, params = {}, isJson = false) {
  let config = {
    url: url,
    method: 'post',
    data: params,
    withCredentials: true
  }
  if (!isJson) {
    // axios默认以json格式(Content-Type: application/json)传递数据
    // 如果需要以Formdata(Content-Type: application/x-www-form-urlencoded)的形式传递需要做以下处理
    Object.assign(config, {data: qs.stringify(params)})
  }
  return new Promise((resolve, reject) => {
    axios.create({})(config)
      .then(res => res.data && (['0', '200'].indexOf(String(res.data.code)) !== -1) ? resolve(res.data) : reject(res.data))
      .catch(err => reject(err))
  })
}

/**
 * 自定义请求
 * @param options 请求配置项
 * options: {
 *    url: 'http://localhost:4000/query', // 请求地址
 *    method: 'get', // 请求方式
 *    data: {a: 1, b: 2}, // post body
 *    params: {a: 1, b: 2}, // url链接参数
 *    withCredentials: true // 是否携带cookie
 *    timeout: 1000, // 请求超时时间
 *    headers: { 'Content-Type': 'application/x-www-form-urlencoded'} // 请求头
 *    onUploadProgress: function (progressEvent) {}, // 上传进度
 *    onDownloadProgress: function (progressEvent) {}, // 下载进度
 * }
 * @returns {Promise<any>} 结果promise
 */
export function httpAjax (options) {
  return new Promise((resolve, reject) => {
    axios.create({})(options)
      .then(res => res.data && (['0', '200'].indexOf(String(res.data.code)) !== -1) ? resolve(res.data) : reject(res.data))
      .catch(err => reject(err))
  })
}

/**
 * jsonp请求
 * @param url 请求路径
 * @param params 入参
 * @returns {Promise<any>} 结果promise
 */
export function httpJsonp (url, params = {}) {
  return new Promise((resolve, reject) => {
    jsonp(url + jsonToString(params), {
      timeout: 10000
      // name: 'callabck'
    }, (err, res) => {
      if (err) {
        reject(err)
      } else {
        return ((['0', '200'].indexOf(String(res.code)) !== -1) || (res.Identity && res.Identity.IsAuthenticated)) ? resolve(res) : reject(res)
      }
    })
  })
}

// 以下是dom相关

/**
 * dom元素绑定事件
 * @param element dom元素
 * @param event 事件名称
 * @param handler 回调
 */
export const on = (function () {
  if (document.addEventListener) {
    return function (element, event, handler) {
      if (element && event && handler) {
        element.addEventListener(event, handler, false)
      }
    }
  } else {
    return function (element, event, handler) {
      if (element && event && handler) {
        element.attachEvent('on' + event, handler)
      }
    }
  }
})()

/**
 * dom元素解绑事件
 * @param element dom元素
 * @param event 事件名称
 * @param handler 回调
 */
export const off = (function () {
  if (document.removeEventListener) {
    return function (element, event, handler) {
      if (element && event) {
        element.removeEventListener(event, handler, false)
      }
    }
  } else {
    return function (element, event, handler) {
      if (element && event) {
        element.detachEvent('on' + event, handler)
      }
    }
  }
})()

/**
 * 判断是否包含对应Class
 * @param el dom元素
 * @param cls 类名
 * @returns {boolean} 是否包含
 */
export function hasClass (el, cls) {
  if (!el || !cls) return false
  if (cls.indexOf(' ') !== -1) throw new Error('className should not contain space.')
  if (el.classList) {
    return el.classList.contains(cls)
  } else {
    return (' ' + el.className + ' ').indexOf(' ' + cls + ' ') > -1
  }
}

/**
 * 为对应元素增加类
 * @param el dom元素
 * @param cls 类名(['class1', 'class2'] or 'class1 class2')
 */
export function addClass (el, cls) {
  if (!el) return
  let curClass = el.className
  let classes = (cls || '').split(' ')

  for (let i = 0, j = classes.length; i < j; i++) {
    let clsName = classes[i]
    if (!clsName) continue

    if (el.classList) {
      el.classList.add(clsName)
    } else if (!hasClass(el, clsName)) {
      curClass += ' ' + clsName
    }
  }
  if (!el.classList) {
    el.className = curClass
  }
}

/**
 * 为对应元素删除类
 * @param el dom元素
 * @param cls 类名(['class1', 'class2'] or 'class1 class2')
 */
export function removeClass (el, cls) {
  if (!el || !cls) return
  let classes = cls.split(' ')
  let curClass = ' ' + el.className + ' '

  for (let i = 0, j = classes.length; i < j; i++) {
    let clsName = classes[i]
    if (!clsName) continue

    if (el.classList) {
      el.classList.remove(clsName)
    } else if (hasClass(el, clsName)) {
      curClass = curClass.replace(' ' + clsName + ' ', ' ')
    }
  }
  if (!el.classList) {
    el.className = curClass.trim()
  }
}

/**
 * 为元素设置样式
 * @param element   元素
 * @param styleName 样式名称 (也可传对象)
 * @param value     样式值
 */
export function setStyle (element, styleName, value) {
  if (!element || !styleName) {
    return
  }
  if (typeof styleName === 'object') {
    for (let prop in styleName) {
      if (styleName.hasOwnProperty(prop)) {
        setStyle(element, prop, styleName[prop])
      }
    }
  } else {
    styleName = styleName.trim()
    element.style[styleName] = value
  }
}

/**
 * number前边用0补位（默认补5位），返回字符串
 * @param num 传入的数字
 * @param length 补多少位0
 */
export function supply0 (num = 0, length = 5) {
  return String(Math.pow(10, length) + num).substring(1)
}

/**
 * compare传入属性，针对对应属性进行排序
 * @param prop 传入属性名（字符串）
 * @param prop2 若传入，则为降序, 不传为升序
 */
export function compare (prop, prop2) {
  return function (obj1, obj2) {
    if (prop2) {
      return (obj1[prop] < obj2[prop]) ? 1 : -1
    } else {
      return (obj1[prop] > obj2[prop]) ? 1 : -1
    }
  }
}

