/**
 * 闲置物品详情页
 * created by qiuwei
 */
import {getUrlParam, toast, toggleLoading, httpJsonp, host} from '../../assets/js/util'

// 商品信息查询接口   cf地址：https://cf.jd.com/pages/viewpage.action?pageId=132106459#id-明星闲置接口文档-六、活动分享页接口
const detailUrl = `${host.promotion}/collect_praise/detail`
// 用户参与活动   cf地址：https://cf.jd.com/pages/viewpage.action?pageId=133239997
const joinUrl = `${host.promotion}/collect_praise/join/submit`

const state = {
  detailInfo: {}, // 详情数据
  serverTime: null // 系统当前时间
}

const getters = {
  // 处理后的详情数据
  data: (state) => {
    let data = state.detailInfo
    // 倒计时
    if (state.serverTime) {
      let now = (new Date()).getTime()
      let deviation = now - state.serverTime
      data.startTime += deviation
      data.endTime += deviation
    }
    return data || {}
  }
}

const mutations = {
  setDetailInfo (state, payload) { state.detailInfo = payload },
  setServerTime (state, payload) { state.serverTime = payload }
}

const actions = {
  /**
   * 清空商品信息
   */
  async clearDetail ({commit}) {
    commit('setDetailInfo', {})
  },
  /**
   * 商品信息查询
   */
  async getDetail ({commit}) {
    commit('setDetailInfo', {})
    commit('setServerTime', null)
    try {
      let res = await httpJsonp(detailUrl, {activityInfoId: getUrlParam('periodCode')})
      // 合并主图和详情图
      res.result.detailImgs = (res.result.icons || []).concat(res.result.detailImgs || [])
      commit('setDetailInfo', res.result)
      commit('setServerTime', res.serverTime)
    } catch (err) {}
  },
  /**
   * 参与活动
   * @returns {Promise<boolean>} 成功还是失败
   */
  async joinAct ({state}) {
    toggleLoading(true)
    let res = false
    try {
      let detailInfo = state.detailInfo
      await httpJsonp(joinUrl, {
        activityInfoId: detailInfo.periodCode,
        goodsId: detailInfo.goodsId,
        starPin: detailInfo.starPin
      })
      res = true
    } catch (err) {
      toast(err.message || '分享失败')
    }
    toggleLoading(false)
    return res
  }

}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}



