<!--弹框模块-->

<template>
  <div class="jd-popup">
    <transition name="tra-back">
      <div class="jd-popup-back" v-show="visible">
        <transition name="tra-panel">
          <div class="jd-popup-panel" v-show="visible">
            <slot></slot>
          </div>
        </transition>
      </div>
    </transition>
  </div>
</template>

<script>
import { forbidScroll } from '../../../assets/js/util'
export default {
  name: 'jd-popup',
  props: {
    visible: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    visible (val) {
      forbidScroll(val)
    }
  },
  methods: {
    // 关闭
    close () {
      this.$emit('update:visible', false)
    }
  }
}
</script>

<style type="text/scss" rel="stylesheet/scss" lang="scss">
.jd-popup {
  &-back {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 999;
    background: rgba(0, 0, 0, .6);
  }
  &-panel {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
  }
  /*背景过渡动画*/
  .tra-back-enter-active, .tra-back-leave-active {
    transition: all ease .3s;
  }
  .tra-back-enter, .tra-back-leave-to {
    opacity: 0;
  }
  /*容器过渡动画*/
  .tra-panel-enter-active, .tra-panel-leave-active {
    transition: all ease .3s;
  }
  .tra-panel-enter, .tra-panel-leave-to {
    opacity: 0;
    transform: translate(-50%, -50%) scale(.5);
  }
}
</style>



