/**
 * 公共部分
 */
import { httpJsonp, getDeviceType, getUrlParam, host } from '../../assets/js/util'

const getWxInfo = '//bizgw.jd.com/wx/getUser' // 根据code获取用户微信信息
const getUserByUin = '//bizgw.jd.com/user/getUserByUin' // 激活拍拍用户
// cf地址：https://cf.jd.com/pages/viewpage.action?pageId=113123580
const getJdInfo = `${host.promotion}/login/queryLogin` // 获取用户京东登录信息

const state = {
  wxInfo: null, // 用户微信信息
  jdInfo: null // 用户京东信息
}

const getters = {
}

const mutations = {
  setWxInfo (state, payload) { state.wxInfo = payload },
  setJdInfo (state, payload) { state.jdInfo = payload }
}

const actions = {
  /**
   * 获取用户微信信息
   * @param payload {code: 用户code}
   */
  async setWxInfo ({commit, state, getters}, payload) {
    try {
      let res = await httpJsonp(getWxInfo, {code: payload.code})
      if (res.data) {
        // 将微信信息存本地存储 有效期3天
        let storage = {
          data: res.data,
          deadline: (new Date()).getTime() + 1000 * 60 * 60 * 24 * 3
        }
        localStorage.setItem('starInfo', JSON.stringify(storage))
      }
      commit('setWxInfo', res.data)
    } catch (err) {}
  },
  /**
   * 获取京东用户信息
   */
  async setJdInfo ({commit, state, getters}, payload) {
    try {
      let res = await httpJsonp(getJdInfo)
      commit('setJdInfo', res.result)
      return res.result
    } catch (err) {}
  },
  /**
   * 判断用户是否已微信授权登录 没有则唤起授权登录
   */
  async judgeWxIn ({state, commit, dispatch}) {
    let url = location.href
    let storageInfo = JSON.parse(localStorage.getItem('starInfo'))
    if (getDeviceType() === 'wx') {
      if (state.wxInfo) {
        return true
      }
      if (storageInfo && storageInfo.deadline > (new Date()).getTime()) {
        commit('setWxInfo', storageInfo.data)
        return true
      }

      let wxCode = getUrlParam('code')
      let isLogin = false
      if (wxCode) {
        await dispatch('setWxInfo', {code: wxCode})
        if (state.wxInfo && state.wxInfo.openid) {
          isLogin = true
        } else {
          url = url.replace(/(&code)=\w+/gi, '')
        }
      }
      if (!isLogin) {
        // 没有code说明是第一次登录 需要授权 直接跳授权页
        // 有code但是没有查询出来用户信息 说明已失效 同样需要跳授权页
        location.href = `//bizgw.jd.com/wx/getUser?returnUrl=${encodeURIComponent(url)}`
        await (new Promise(resolve => { setTimeout(() => { resolve() }, 1000) }))
      } else {
        return true
      }
    }
  },
  /**
   * 判断用户是否已京东登录 没有则跳登录页
   */
  async judgeJdIn ({state, dispatch}, payload = {}) {
    let url = payload.url || location.href
    let type = getDeviceType()
    if (!(state.jdInfo || await dispatch('setJdInfo'))) {
      if (type === 'paipaiApp') {
        location.href = `openapp.paipai://showLoginViewController/app?param=${encodeURIComponent(JSON.stringify({ key1: url }))}`
      } else if (type === 'wx' || type === 'qq') {
        location.href = `//wq.jd.com/pinbind/pintokenredirect?biz=huishou&url=${encodeURIComponent(url)}`
      } else {
        location.href = `//plogin.m.jd.com/user/login.action?appid=177&returnurl=${encodeURIComponent(url)}`
      }
    } else {
      await httpJsonp(getUserByUin, {client: type === 'wx' ? 'wxweb' : (type === 'qq' ? 'qq' : '')})
      return true
    }
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}



