<!-- 四级地址选择 -->
<template>
  <div class="jd-address" v-show="show">
    <div class="address-mask">
      <div class="otherArea" @click="close"></div>
      <div class="address-dialog">
        <div class="address-dialog-title">
          <span>收货地址</span>
          <i></i>
        </div>
        <div class="address-dialog-con">
          <!-- <span class="loading" v-if="loading"><i></i></span> -->
          <ul class="con-select">
            <li :class="showProvince?'active':''" @click="getArea('showProvince')">{{province}}</li>
            <li :class="showCity?'active':''" v-if="choiceCity" @click="getArea('showCity')">{{city}}</li>
            <li :class="showCounty?'active':''" v-if="choiceCounty" @click="getArea('showCounty')">{{county}}</li>
            <li :class="showTown?'active':''" v-if="choiceTown" @click="getArea('showTown')">{{town}}</li>
          </ul>
          <!-- 省列 -->
          <transition name="fade">
            <ul class="address-province area clearfix" v-if="showProvince">
              <li v-for="item in base.provinceArr" @click="getCity(item.id,item.name)" :class="item.name === province ? 'selected' : ''" :key="item.id">
                {{item.name}}<i v-if="item.name === province"></i>
              </li>
            </ul>
          </transition>
          <!-- 市列 -->
          <transition name="fade">
            <ul class="address-city area clearfix" v-if="showCity">
              <li v-for="item in base.cityArr" @click="getCounty(item.id,item.name)" :class="item.name === city ? 'selected' : ''" :key="item.id">
                {{item.name}}<i v-if="item.name === city"></i>
              </li>
            </ul>
          </transition>
          <!-- 区列 -->
          <transition name="fade">
            <ul class="address-county area clearfix" v-if="showCounty">
              <li v-for="item in base.countyArr" @click="getTown(item.id,item.name)" :class="item.name === county ? 'selected' : ''" :key="item.id">
                {{item.name}}<i v-if="item.name === county"></i>
              </li>
            </ul>
          </transition>
          <!-- 县列 -->
          <transition name="fade">
            <ul class="address-town area clearfix" v-if="showTown">
              <li v-for="item in base.townArr" :key="item.id" @click="getLast(item.id,item.name)">
                {{item.name}}<i v-if="item.name === town"></i>
              </li>
            </ul>
          </transition>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import areaHandle from './area.js'

export default {
  name: 'jd-address',
  data () {
    return {
      areaState: areaHandle.state, // 从组件内引入的地址所有数据，有缓存
      loading: false,
      address: {}, // 选中的省市区县
      show: false,
      province: '请选择', // 选择的省名称
      provinceId: undefined,
      city: '请选择', // 选择的市名称
      cityId: undefined,
      county: '请选择', // 选择的区名称
      countyId: undefined,
      town: '请选择', // 最后一级请选择
      townId: undefined,
      choiceProvince: false, // 省按钮的显示隐藏
      choiceCity: false, // 市按钮的显示隐藏
      choiceCounty: false, // 区按钮的显示隐藏
      choiceTown: false, // 镇按钮的显示隐藏
      showProvince: true, // 显示省列
      showCity: false, // 显示市列
      showCounty: false, // 显示区列
      showTown: false // 显示镇列
    }
  },
  computed: {
    base () {
      return {
        provinceArr: this.areaState.province,
        cityArr: this.provinceId ? areaHandle.areaArrMap(this.provinceId) : [],
        countyArr: this.cityId ? areaHandle.areaArrMap(this.cityId) : [],
        townArr: this.countyId ? areaHandle.areaArrMap(this.countyId) : []
      }
    }
  },
  watch: {
    'provinceId' (val) {
      areaHandle.cacheArea('city', val)
    },
    'cityId' (val) {
      areaHandle.cacheArea('county', val)
    }
  },
  methods: {
    // 点击tab
    getArea (k) {
      ['showCity', 'showCounty', 'showTown', 'showProvince'].forEach(e => { this[e] = false })
      this[k] = true
    },
    // 点击选择省后
    async getCity (pid, pname) {
      this.province = pname // 选中的省
      this.provinceId = pid
      this.showProvince = false // 省列消失
      this.choiceCity = true // 显示市按钮
      this.showCity = true // 显示市列
      this.city = '请选择'
      this.choiceCounty = false // 隐藏区(非第一次选择)
      this.choiceTown = false // 隐藏镇(非第一次选择)
    },
    // 点击选择市后
    async getCounty (cid, cname) {
      this.city = cname // 选中的市
      this.cityId = cid
      this.showCity = false // 市列消失
      this.choiceCounty = true // 显示区按钮
      this.showCounty = true // 显示区列
      this.county = '请选择'
      this.choiceTown = false // 隐藏镇(非第一次选择)
    },
    // 点击选择区后
    async getTown (coid, coname) {
      this.county = coname // 选中的区
      this.countyId = coid
      // 需要判断有没有四级地址，如果请求失败，走catch的部分，直接到此为止。如果请求成功，则显示四级地址
      try {
        await areaHandle.cacheArea('town', coid)
        this.showCounty = false // 区列消失
        this.showTown = true // 显示镇列
        this.choiceTown = true // 显示镇按钮
      } catch (error) {
        this.town = ''
        this.townId = undefined
        this.upData()
      }
    },
    // 点击选择镇后
    getLast (tid, tname) {
      this.town = tname
      this.townId = tid
      this.upData()
    },
    // 传值给父组件
    upData () {
      let addressNew = {
        province: this.province,
        city: this.city,
        county: this.county,
        town: this.town,
        provinceId: this.provinceId,
        cityId: this.cityId,
        countyId: this.countyId,
        townId: this.townId
      }
      Object.assign(this.address, addressNew)
      this.$emit('address', this.address) // 选中的值传给父组件
      this.show = false
    },
    // 关闭
    close () {
      this.show = false
    }
  }
}
</script>
<style type="text/scss" rel="stylesheet/scss" lang="scss">
.jd-address {
  .address-mask {
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, .5);
    position: fixed;
    left: 0;
    top: 0;
    z-index: 99;
    .otherArea {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0);
    }
    .address-dialog {
      width: 100%;
      height: 980px;
      overflow: hidden;
      position: absolute;
      left: 0;
      bottom: 0;
      background: #fff;
      animation:mymove 300ms 1;
      -webkit-animation:mymove 300ms 1; /* Safari and Chrome */
      z-index: 999;
      &-title {
        width: 100%;
        height: 112px;
        line-height: 112px;
        text-align: center;
        span {
          font-size: 30px;
          color: #333333;
        }
        i {}
      }
      &-con {
        .loading {
          position: absolute;
          top: 50%;
          left: 48%;
          i {
            display: inline-block;
            width: 50px;
            height: 50px;
            background: url(data:image/gif;base64,) no-repeat;
            background-size: cover;
          }
        }
        ul.con-select {
          height: 88px;
          border-bottom: 1px solid #F1F1F1;
          padding-left: 32px;
          li {
            float: left;
            font-size: 28px;
            color: #333333;
            margin-right: 60px;
            border-bottom: 5px solid #fff;
            height: 84px;
            line-height: 84px;
            max-width: 142px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            &.active {
              color: #FF3434;
              border-bottom-color: #FF3434;
            }
            &:last-child {
              margin-right: 0;
            }
          }
        }
        ul.area {
          height: 780px;
          overflow: hidden;
          overflow-y: auto;
          -webkit-overflow-scrolling: touch; /* ios5+ */
          li {
            float: left;
            padding-left: 32px;
            width: 100%;
            height: 88px;
            line-height: 88px;
            font-size: 28px;
            color: #333333;
            i {
              background: url(../../../assets/img/select@3x.png) no-repeat;
              background-size: 100%;
              display: inline-block;
              width: 24px;
              height: 17px;
              margin-left: 16px;
            }
            &.selected {
              font-weight: bold;
            }
          }
        }
      }
    }
  }
}
@keyframes mymove {
  0% {bottom: -980px;}
  100% {bottom: 0;}
}

@-webkit-keyframes mymove {
  0% {bottom: -980px;}
  100% {bottom: 0;}
}
.fade-enter-active {
  transition: all .3s ease;
}
/*.fade-leave-active {
  transition: all .1s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}*/
.fade-enter {
  transform: translateX(450px);
  opacity: 0;
}
.fade-leave-to {
  transform: translateX(0px);
  opacity: 0;
}
</style>



