import 'babel-polyfill'
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
// import Icon from 'vue-svg-icon/Icon.vue'
import useAllJdUI from './components'
import VueLazyload from 'vue-lazyload'
import { forbidScroll, setTitle, getDeviceType } from './assets/js/util'
// import animate from 'animate.css'

Vue.config.productionTip = false

// 加载svg图标库
// Vue.component('icon', Icon)

// 引用自定义组件
useAllJdUI()

// 加载图片懒加载
Vue.use(VueLazyload, { preLoad: 1.3, attempt: 1 })

// animate.css动画
// Vue.use(animate)

router.beforeEach((to, from, next) => {
  let type = getDeviceType()
  if (type !== 'qq') {
    // title更新
    let title = (to.meta || {title: '明星好物集赞送'}).title
    document.title = title
    if (type === 'paipaiApp') {
      setTitle(title)
    }
  }
  // 清除滚动禁止
  forbidScroll(false)
  next()
})

/**
 * js中加载assets中的图片
 * @param imgName 图片名称 例：userDefault.png
 */
Vue.prototype.$reqImg = (imgName) => {
  return require(`./assets/img/${imgName}`)
}

/**
 * 图片地址处理 如果以http开头的则无需拼//img20.360buyimg.com/n1/s90x90_ 这种
 * @param imgSrc 图片src 例：http://xxxxx.png 或者 jfs/t3912e4Ne204d26a.jpg
 * @param size 图片尺寸 例：90x90
 * @param space 命名空间 例：n1  ppershou
 * @param cut 是否裁剪
 */
Vue.prototype.$supImg = (imgSrc, size, space = 'n1', cut = true) => {
  if (!imgSrc) {
    return ''
  }
  if (/^http/.test(imgSrc) && /ppershou/.test(imgSrc)) {
    // 拍拍app上传头像过大 需要裁剪
    return `//img10.360buyimg.com/${space}/s120x120_${imgSrc.substring(imgSrc.indexOf('jfs'))}!cc_1x1`
  } else {
    if (/^http/.test(imgSrc)) {
      return imgSrc
    } else if (size) {
      if (cut) {
        return `//img10.360buyimg.com/${space}/${'s' + size + '_'}${imgSrc}!cc_${size}`
      } else {
        return `//img10.360buyimg.com/${space}/${'s' + size + '_'}${imgSrc}`
      }
    } else {
      return `//img10.360buyimg.com/${space}/${imgSrc}`
    }
  }
}

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



