/**
 * 往期精彩活动页
 * created by qiuwei
 */
import {host, httpJsonp, toast} from '../../assets/js/util'

// 信息查询接口   cf地址：https://cf.jd.com/pages/viewpage.action?pageId=132106459#id-明星闲置接口文档-五、往期活动接口
const listUrl = `${host.promotion}/collect_praise/history`

const state = {
  list: [], // 商品列表
  allLoad: {is: false}, // 是否所有页都加载完成
  pageNo: 1, // 当前页
  pageSize: 10 // 一页数量
}

const getters = {
}

const mutations = {
  setList (state, payload) { state.list = state.list.concat(payload) },
  setAllLoad (state, payload) { state.allLoad.is = payload },
  setPageNo (state, payload) { state.pageNo = payload }
}

const actions = {
  /**
   * 页面信息查询
   */
  async loadData ({state, commit}) {
    try {
      let res = await httpJsonp(listUrl, {pageNo: state.pageNo, pageSize: state.pageSize})
      // 纠正时间误差
      if (res.serverTime) {
        let now = (new Date()).getTime()
        let deviation = now - res.serverTime
        ;((res.result || {pageList: []}).pageList || []).forEach(e => {
          e.startTime = e.startTime + deviation
          e.endTime = e.endTime + deviation
        })
      }
      // 合并主图和详情图
      let list = (res.result || {pageList: []}).pageList || []
      list.forEach(e => {
        e.detailImgs = (e.icons || []).concat(e.detailImgs || [])
      })
      commit('setList', list)
      commit('setPageNo', state.pageNo + 1)
      if (res.result.totalCount <= state.list.length) {
        commit('setAllLoad', true)
      }
    } catch (err) {
      toast(err.message || '数据读取失败')
    }
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}



