<!--数字动画模块-->

<template>
  <span class="jd-integer">{{ tweeningValue }}</span>
</template>

<script>
import TWEEN from 'tween.js'
export default {
  name: 'jd-integer',
  props: {
    // 变动的值
    value: {
      type: Number,
      required: true
    },
    // 变动的时间
    duration: {
      type: [String, Number],
      default: 500
    }
  },
  data: function () {
    return {
      tweeningValue: 0
    }
  },
  watch: {
    value: function (newValue, oldValue) {
      if (newValue > oldValue) {
        this.tween(oldValue, newValue)
      } else {
        this.oldValue = this.newValue
      }
    }
  },
  mounted: function () {
    this.tween(0, this.value)
  },
  methods: {
    tween: function (startValue, endValue) {
      let vm = this
      function animate () {
        if (TWEEN.update()) {
          requestAnimationFrame(animate)
        }
      }
      new TWEEN.Tween({ tweeningValue: startValue })
        .to({ tweeningValue: endValue }, Number(this.duration))
        .onUpdate(function () {
          vm.tweeningValue = this.tweeningValue.toFixed(0)
        })
        .start()
      animate()
    }
  }
}
</script>
<style type="text/scss" rel="stylesheet/scss" lang="scss">
  .jd-integer {
    font-family: 'jdzh-r';
  }
</style>



